    Description:
        Purpose of the stylesheet follows.
*/
html,
body {
    padding: 0px;
    margin: 0px;
    width: 100%;
    height: 100%;
}

.fill {
    margin: 0px;
    padding: 0px;
    width: 100%;
    height: 100%;
}

body {}

select {
    cursor: pointer;
}

label {
    font-weight: normal;
}

input[type=text] {
    font-weight: normal;
}


#bodyContent {
    position: relative;
}

#bodyContent.navbar-adjust {
    height: calc(100% - 55px);
    top: 55px;
}

.disabled-background {
    background-color: #FFF6F4;
}

#mainNavbar {
    padding-top: 3px;
}

.edit-video-source-container {
    padding: 10px;
}

.play-back-button {
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 10000000
}

.form input.ng-valid.ng-touched {
    background-color: #dff0d8;
}

.form input.ng-invalid.ng-touched {
    background-color: #f2dede;
}

.loading {
    background-image: url('../assets/img/ajax-loader.gif');
    height: 32px;
    width: 32px;
    background-color: transparent;
    display: inline-block;
    margin: 0;
    padding: 0;
    vertical-align: middle;
}


#mainNavbar {
    margin: 0px;
    border-radius: 0;
    position: fixed;
    width: 100%;
    top: 0px;
    z-index: 1;
}

.brand {
    color: purple
}

a {
    cursor: pointer;
}

body {
    padding: 0px;
    border: 0px solid red;
    height: 100%;
}

.pointer {
    cursor: pointer;
}

.marginless {
    margin: 0;
}

.text-center,
[text-center] {
    text-align: center;
}

.text-right {
    text-align: right;
}

.text-left {
    text-align: left;
}

#bodyPadding {
    height: 50px;
    width: 100%;
}

#containerRelativer {
    position: relative;
}

.radio {
    padding-left: 0px;
}

.videoRow .sd {
    height: 80px;
    margin-top: 10px;
    max-width: none;
    width: auto;
}

.videoRow .hd {
    height: 100px;
    max-width: none;
    width: auto;
}

.tableScrollArea {}

.table th {
    white-space: nowrap;
}

#logTable {
    width: 100%;
}

#logTable th {
    border-left: 2px solid white;
    borter-right: 2px solid white;
}

#logTable td,
#logTable th {
    padding-left: 10px;
    padding-right: 10px;
}

.tile:hover {
    text-decoration: none;
}

input[type=radio],
label {
    display: inline;
    line-height: 20px;
    margin: 0px;
}

#episodeInfo {
    background-color: white;
    border: 0px solid black;
    width: 500px;
    min-height: 250px;
    padding: 20px;
    padding-top: 7px;
}

#episodeInfo * {
    color: black;
}

.episodeRow {
    cursor: pointer;
    color: black;
}

.episodeRow:hover {
    background-color: lightgrey;
}

.episodeRow.selected {
    background-color: grey;
    color: white;
}

.nextEpisodeRow {
    background-color: #ffcccc;
}

.shadow {
    -webkit-box-shadow: 3px 3px 50px 1px #555555;
    box-shadow: 3px 3px 50px 1px #555555;
    /*-moz-box-shadow: 3px 3px 4px #000;*/
    /* For IE 8 */
    -ms-filter: "progid:DXImageTransform.Microsoft.Shadow(Strength=4, Direction=135, Color='#000000')";
    /* For IE 5.5 - 7 */
    filter: progid:DXImageTransform.Microsoft.Shadow(Strength=4, Direction=135, Color='#000000');
}

.progressbar {
    width: 100px;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    background-color: red;
    height: 10px;
    margin-left: 20px;
    margin-right: 10px;
    position: relative;
}

.percentWatched {
    height: 100%;
    background-color: green;
    text-align: center;
}

.percentWatchedText {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-color: transparent;
    text-align: center;
    padding: 0px;
    margin: 0px;
    font-size: 10px;
    background-color: transparent;
    line-height: 10px;

}

#videoInfo {
    float: left;
    display: inline;
}

.playlist {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.playlist li {
    float: left;
    cursor: pointer;
    width: 160px;
    height: 250px;
    border: 1px solid black;
    background-color: #DFDFDF;
    text-align: center;
    margin: 5px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
}

.playlist li h3 {
    font-size: 20px;
    margin: 0px;
    padding: 0px;
}

#playbackFinished {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    display: none;
    background-color: rgba(255, 0, 0, 0.5);
    filter: progid:DXImageTransform.Microsoft.Gradient(GradientType=0, StartColorStr='#7F00FF00', EndColorStr='#7F00FF00');
    background-color: white;
}

label {
    width: 100px;
}

.error {
    color: red;
}

.full-width {
    width: 100%;
}

#tablesArea {
    border: 1px solid black;
    min-height: 300px;
    overflow: auto;
    margin: 0px;
    padding: 0px;
}

#playTitle {
    margin: 0px;
    padding: 0px;
    line-height: 0px;
    width: 100%;
    display: inline-block;
    font-size: 15px;
    text-align: center;
}

.relative {
    position: relative;
}

#videoInfoPosterColumn {
    display: inline;
    display: inline-block;
    width: 200px;
}

#videoInfoPosterColumn .margin {
    margin: 5px 0px;
}

#videoInfoPosterColumn {}

.video-container {
    margin-right: auto;
    margin-left: auto;
    text-align: center;
}


/* extra small devices (less than 768px)*/
@media (max-width:767px) {
    .navbar-form.navbar-left {
        display: inline;
    }
}

label,
input[type=radio] {
    cursor: pointer;
}

#metadataSearchResults {
    text-align: center;
    padding: 5px;
    position: relative;
}

#metadataSearchResults .metadata-tile {
    padding: 5px;
    border: 1px solid grey;
    border-radius: 5px;
    display: inline-block;
    width: 100%;
    background-color: lightgrey;
    margin: 5px;
}

#metadataSearchResults .metadata-tile:hover {
    cursor: pointer;
    padding: 2px;
    border: 4px solid grey;

}

#metadataSearchResults .metadata-tile .text-area {
    padding-left: 5px;
    text-align: left;
}

#metadataSearchResults .metadata-tile img {
    width: 100%;
}

.non-bold {
    font-weight: normal;
}

#searchByRow label {
    padding-right: 10px;
}

.wait-small {
    background-image: url('../assets/img/ajax-loader.gif');
    background-size: 24px 24px;
    height: 24px;
    width: 24px;
    border-style: none;
    background-color: transparent;
    border-collapse: collapse;
    border-spacing: 0;
    outline: none;
    border: none 0;
    display: inline;
    display: inline-block;
    vertical-align: middle;
}